import * as React from 'react';
import {useState} from 'react';
import {withNamespaces, WithNamespaces} from "react-i18next";
import {Button, ButtonGroup, Modal} from "semantic-ui-react";
import {useGraph} from "../../hooks/useGraph";
import {BoardId} from "../../model/Board";
import {Node, NodeType} from "../../model/Graph";
import {dddActionFromNode, isDDDAction} from "../../service/cody-wizard/context/ddd-action";
import {WizardContext} from "../../service/cody-wizard/context/wizard-context";
import {isAggregateEvent, isAggregateEventCandidate} from "../../service/cody-wizard/event/is-aggregate-event";
import {isValid} from "../../service/cody-wizard/node/is-valid";
import {stepHeader} from "../../service/cody-wizard/step/step-header";
import {stepWikiLink} from "../../service/cody-wizard/step/step-wiki-link";
import {isListForUICandidate} from "../../service/cody-wizard/vo/is-list-vo";
import {isStateVoCandidate} from "../../service/cody-wizard/vo/is-state-vo-candidate";
import ActiveStepIcon from "./Step/ActiveStepIcon";
import AggregateEvent from "./Step/AggregateEvent";
import NextStep from "./Step/NextStep";
import PreviousStep from "./Step/PreviousStep";
import StateVO from "./Step/StateVO";
import UnsupportedNode from "./Unsupported/UnsupportedNode";
import Aggregate from "./Step/Aggregate";
import AggregateCommand from "./Step/AggregateCommand";
import {isUiContext, uiContextFromNode} from "../../service/cody-wizard/context/ui";
import {isDddActionUi} from "../../service/cody-wizard/ui/is-ddd-action-ui";
import Ui from "./Step/Ui";

export const DEFAULT_CONNECT_MARGIN = 80;
export const INPUT_CHAR_SIZE = 9;
export const CHAR_SIZE_UNIT = 'px';
export const ADDITIONAL_CHAR_PADDING = 10;

export type WizardStep = 'state' | 'command' | 'aggregate' | 'aggregateEvent' | 'policyEvent' | 'publicEvent' | 'ui' | 'feature' | 'listForUI' | 'anyVO';

export type FinishStep = () => WizardContext;

export interface WithWizardStepProps<CTX extends WizardContext>{
  boardId: BoardId;
  ctx: CTX;
  onCtxChanged: (ctx: CTX) => void;
  onFinish: (finishAction: FinishStep) => void;
}

interface OwnProps {
  boardId: BoardId;
  openHandle: (handle: (open: boolean, selectedNode: Node | null) => void) => void;
  onClose: () => void;
}

type WizardModalProps = OwnProps & WithNamespaces;

let currentFinishStepAction: FinishStep | null = null;

const EMPTY_CONTEXT: WizardContext = {type: "empty", nextStep: () => null, previousStep: () => null};

const WizardModal = (props: WizardModalProps) => {
  const [open, setOpen] = useState(false);
  const [ctx, setCtx] = useState<WizardContext>(EMPTY_CONTEXT)
  const [unsupportedNode, setUnsupportedNode] = useState<Node|null>(null);
  const [activeStep, setActiveStep] = useState<WizardStep | null >(null);
  const [graph] = useGraph();

  const memoizeNode = (node: Node, setWizardStep: boolean = false): void => {
    let wizardStep: WizardStep | null = null;

    switch (node.getType()) {
      case NodeType.command:
        setCtx(dddActionFromNode(node));
        wizardStep = 'command';
        break;
      case NodeType.aggregate:
        setCtx(dddActionFromNode(node));
        wizardStep = 'aggregate';
        break;
      case NodeType.event:
        setCtx(dddActionFromNode(node));

        if(isAggregateEvent(node)) {
          wizardStep = 'aggregateEvent';
        } else if (isAggregateEventCandidate(node)) {
          // @TODO: Introduce Wizard Step Overivew to select between: aggregate, public, policy
          wizardStep = 'aggregateEvent';
        } else {
          wizardStep = 'aggregateEvent';
        }

        break;
      case NodeType.ui:
        if(isDddActionUi(node)) {
          setCtx(dddActionFromNode(node));
        } else {
          setCtx(uiContextFromNode(node));
        }
        wizardStep = 'ui';
        break;
      case NodeType.document:
        if(isStateVoCandidate(node)) {
          setCtx(dddActionFromNode(node));
          wizardStep = 'state';
          break;
        }

        if(isListForUICandidate(node)) {
          setCtx(uiContextFromNode(node));
          wizardStep = 'listForUI';
          break;
        }

        setCtx(dddActionFromNode(node));
        wizardStep = 'anyVO';

        break;
      case NodeType.feature:
        setCtx(dddActionFromNode(node));
        wizardStep = 'feature';
        break;
      default:
        setUnsupportedNode(node);
    }

    if(setWizardStep) {
      setActiveStep(wizardStep);
    }
  }

  props.openHandle((openFromHandle, node) => {

    setOpen(openFromHandle);

    if(node) {
      if(isValid(node)) {
        memoizeNode(node, !activeStep);
      } else {
        setUnsupportedNode(node);
        setCtx(EMPTY_CONTEXT);
      }
    }
  });

  const resetWizard = () => {
    setCtx(EMPTY_CONTEXT);
    setUnsupportedNode(null);
    setActiveStep(null);
    currentFinishStepAction = null;
  }

  const closeWizard = () => {
    resetWizard();
    props.onClose();
  }

  const saveCurrentStep = () => {
    if(currentFinishStepAction) {
      setCtx(currentFinishStepAction());
    }
  }

  const changeStep = (step: WizardStep) => {
    saveCurrentStep();
    setActiveStep(step);
  }

  const finish = () => {
    saveCurrentStep();
    closeWizard();
  }

  const helpLink = stepWikiLink(activeStep);

  return <Modal open={open} closeIcon={true} onClose={finish} size="large" closeOnEscape={false} >
    <Modal.Header>
      Cody Wizard{activeStep? ': ' + stepHeader(activeStep) : ''}&nbsp;<ActiveStepIcon activeStep={activeStep} />
      {helpLink && <Button as={'a'} href={helpLink} target={'_blank'} floated="right" circular={true} basic={true} icon="question" size="mini"/>}
    </Modal.Header>
    <Modal.Content scrolling={true}>
      {unsupportedNode && <UnsupportedNode node={unsupportedNode} />}
      {!unsupportedNode
        && activeStep === 'state'
        && isDDDAction(ctx)
        && <StateVO boardId={props.boardId} ctx={ctx} onFinish={save => currentFinishStepAction = save} onCtxChanged={setCtx} />}
      {!unsupportedNode
        && activeStep === 'aggregateEvent'
        && isDDDAction(ctx)
        && <AggregateEvent boardId={props.boardId} ctx={ctx} onFinish={save => currentFinishStepAction = save} onCtxChanged={setCtx} />}
      {!unsupportedNode
        && activeStep === 'aggregate'
        && isDDDAction(ctx)
        && <Aggregate boardId={props.boardId} ctx={ctx} onFinish={save => currentFinishStepAction = save} onCtxChanged={setCtx} />}
      {!unsupportedNode
        && activeStep === 'command'
        && isDDDAction(ctx)
        && <AggregateCommand boardId={props.boardId} ctx={ctx} onFinish={save => currentFinishStepAction = save} onCtxChanged={setCtx} />}
      {!unsupportedNode
        && activeStep === 'ui'
        && (isDDDAction(ctx) || isUiContext(ctx))
        && <Ui boardId={props.boardId} ctx={ctx} onFinish={save => currentFinishStepAction = save} onCtxChanged={setCtx} />}
    </Modal.Content>
    <Modal.Actions>
      <ButtonGroup floated="left">
        <PreviousStep activeStep={activeStep} ctx={ctx} onPreviousStep={step => changeStep(step)} />
        <NextStep activeStep={activeStep} ctx={ctx} onNextStep={step => changeStep(step)} />
      </ButtonGroup>
      <Button primary={true} icon="checkmark" content={props.t('app.form.finish') as string} onClick={finish} />
    </Modal.Actions>
  </Modal>
};

export default withNamespaces()(WizardModal);
